/* custom-swal-styles.css */
.custom-swal-popup {
    background-color: #0f1b35;
    color: #e0ffff;
    border-radius: 15px;
    box-shadow: 0px 5px 15px #0000004d;
}
  
.login-modal .custom-swal-title {
    color: #e0ffff;
    font-family: Arial, sans-serif;
}

/* Más específicos para SweetAlert2 */
.swal2-confirm.custom-swal2-confirm {
    --color: #2dcf22;
    --color2: #0f1b35;
    padding: 0.8em 1.75em;
    background-color: transparent;
    border-radius: 6px;
    border: .3px solid var(--color);
    transition: .5s;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    z-index: 1;
    font-weight: bold;
    font-size: 15px;
    font-family: 'Roboto', 'Segoe UI', sans-serif;
    text-transform: uppercase;
    color: var(--color);
}

.swal2-confirm.custom-swal2-confirm::after, .swal2-confirm.custom-swal2-confirm::before {
    content: '';
    display: block;
    height: 100%;
    width: 100%;
    transform: skew(90deg) translate(-50%, -50%);
    position: absolute;
    inset: 50%;
    left: 25%;
    z-index: -1;
    transition: .5s ease-out;
    background-color: var(--color);
}
.swal2-confirm.custom-swal2-confirm::before {
    top: -50%;
    left: -25%;
    transform: skew(90deg) rotate(180deg) translate(-50%, -50%);
}

.swal2-confirm.custom-swal2-confirm:hover::before {
    transform: skew(45deg) rotate(180deg) translate(-40%, -50%);
}

.swal2-confirm.custom-swal2-confirm:hover::after {
    transform: skew(45deg) translate(-40%, -50%);
}

.swal2-confirm.custom-swal2-confirm:hover {
    color: var(--color2);
}

.swal2-confirm.custom-swal2-confirm:active {
    filter: brightness(.7);
    transform: scale(.98);
}

.swal2-cancel.custom-swal2-cancel {
