.background-container-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background-color: hsla(204, 0%, 100%, 1);
  background-image: radial-gradient(circle at 0% 0%, hsla(207.2995861876241, 100%, 25%, 1) 3%, transparent 70%), radial-gradient(circle at 100% 0%, hsla(207.2412045062839, 100%, 40%, 1) 3%, transparent 39%), radial-gradient(circle at 60% 102%, hsla(207.08737864077668, 100%, 32%, 1) 0%, transparent 134%);
  background-blend-mode: normal, normal, normal;
}
.background-container-menu.drawerOpen {
  width: 100%;
  height: 100%;
  /* Agrega otros estilos específicos cuando el cajón está abierto si es necesario */
}
#caja_contenido {
  flex: 1; /* El contenido principal ocupa el espacio restante */
  overflow-y: auto; /* Permite desplazamiento vertical si es necesario */
  max-height: 100vh;
}
.content-open {
  margin-left: 310px; /* El 50px representa el ancho del menú cuando está cerrado */
}

.content-closed {
  margin-left: 50px; /* Ajusta el margen izquierdo cuando el drawer está cerrado */
}
.custom-drawer .MuiPaper-root {
  background-color: #0f1b35; 
  border: 2.5px solid #0f1b35; /* Ajusta el grosor del borde */
  width: 310px; /* Ajusta esta parte en JavaScript para el drawer abierto/cerrado */
  transition: width 0.3s;
}

.custom-drawer.drawer-closed .MuiPaper-root {
  width: 50px;
}

.custom-drawer .MuiListItemIcon-root {
  color: #e2e2e2;
}

.custom-drawer .MuiList-root {
  width: 100%; /* Ajusta esta parte en JavaScript para el drawer abierto/cerrado */
  transition: width 0.3s;
}

.custom-drawer.drawer-closed .MuiList-root {
  width: 0%;
}
.image-grid-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Ajusta la altura según tu necesidad */
}

.image-row {
  display: flex;
  justify-content: center;
  align-items: center;
}

.grid-image {
  max-width: 33%; /* Ajusta el tamaño de las imágenes según tu diseño */
  margin: 10px; /* Espacio entre las imágenes */
}
/* estilos.css */
.custom-form {
  border: 2px solid #e2e2e2;
  box-sizing: border-box;
  width: 100%;
  border-radius: 10px;
  padding: 2.5%;
  margin: 0 auto;
  background-color: #0f1b35;
}
.custom-form2 {
  border: 2px solid #e2e2e2;
  box-sizing: border-box;
  width: 100%;
  border-radius: 10px;
  padding: 2.5%;
  margin-top: 1%;
  margin-bottom: 1%;
  background-color: #0f1b35;
}
.custom-validation-message {
  color: #ff0000; /* Color de texto rojo */
  background-color: #ffe5e5; /* Fondo ligeramente rojo */
  padding: 0.45%;
  font-weight: bold;
}
/* En tu archivo CSS global o en un archivo CSS en módulos */
#caja_menu::-webkit-scrollbar {
  width: 28px; /* Ajusta el ancho del scrollbar */
}

#caja_menu::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.5); /* Color del scrollbar */
  border-radius: 40px; /* Bordes redondeados */
}

#caja_menu::-webkit-scrollbar-track {
  background: transparent; /* Fondo del track del scrollbar */
}
